import React, { useState } from 'react'
import { ComponentsProvider, Heading, Page, Space, SpaceVertical } from '@looker/components'
import { ExploreSelector } from './components/ExploreSelector'

export const App: React.FC = () => {
  const [selectedExplore, setSelectedExplore] = useState<string>('')

  const handleExploreSelect = (explore: string) => {
    setSelectedExplore(explore)
  }

  return (
    <ComponentsProvider>
      <Page>
        <SpaceVertical gap="large">
          <Space>
            <Heading fontWeight="semiBold">ExploreWise</Heading>
          </Space>
          <Space>
            <Heading as="h4" fontWeight="medium">Analyze and optimize your Looker Explores for Conversational Analytics.</Heading>
          </Space>
          <ExploreSelector onExploreSelect={handleExploreSelect} />
          {selectedExplore && (
            <Space>
              <Heading as="h3">Selected Explore: {selectedExplore}</Heading>
            </Space>
          )}
        </SpaceVertical>
      </Page>
    </ComponentsProvider>
  )
} 